<template>
	<view class="box marginTop">
		 <popup v-if="show" @back="back"  :show="show" :tableData="data"></popup>
		<view class="title">
			操作票处理
		</view>
		<view class="operation">
			<view class="operationItem margin" v-for="(item,index) in data">
             <view class="itemData title">
				{{item.name}}
             </view>
			 <view class="IiemBox">
			 	<view class="itemUi">
			 		<el-checkbox-group v-model="data[index].people" :min="1" :max="10" @change="(val) => change(val, index)">
			 			<el-checkbox v-for="city in data[index].cities" :label="city" :key="city"></el-checkbox>
			 		</el-checkbox-group>
			 	</view>
			 	<view class="itemSum">
			 		<el-popover placement="left-start" width="200" trigger="click" :value="true">
			 			<view class="choose">
			 				你已经选择了{{data[index].len}}人
			 			</view>
			 			<view class="choose">
			 				<text v-for="(item,index) in data[index].people" :key="index">{{item}},</text>
			 			</view>
			 			<!-- <el-button slot="reference">click 激活</el-button> -->
			 		</el-popover>
			 	</view>
			 </view>
			</view>
			<view class="btn button">
				<el-button type="primary" plain @click="btns()">主要按钮</el-button>
			</view>
		</view>
	</view>
</template>

<script>
	import popup from "./popup.vue"
	const cityOptions = ['张三', '李四', '王五', '牟六', '请求', '我我', '嗯嗯', '让人', '引用', '哦哦'];
	export default {
		name: "operation",
		data() {
			return {
				checkedCities: [],
				show:false,
				data: [{
					    name:'周一',
						people: [],
						len: 0,
						cities: cityOptions
					},
					{
						 name:'周二',
						people: [],
						len: 0,
						cities: cityOptions
					},
					{
						 name:'周三',
						people: [],
						len: 0,
						cities: cityOptions
					},
					{
						 name:'周四',
						people: [],
						len: 0,
						cities: cityOptions
					},
					{
						 name:'周五',
						people: [],
						len: 0,
						cities: cityOptions
					}, {
						 name:'周六',
						people: [],
						len: 0,
						cities: cityOptions
					},
					{
						 name:'周天',
						people: [],
						len: 0,
						cities: cityOptions
					}
				]
			};
		},
		methods: {
			change(e,index) {
				console.log(e)
				console.log(index)
				this.data[index].people = e
				this.data[index].len = e.length
			},
			btns(){
				console.log("是是是")
				let data=this.data
				let flag=true;
				for(let i=0;i<data.length;i++){
					let string=''
					if(data[i].people.length==0){
							this.$message({
									message:data[i].name+'信息未填写完整',
									type:"error"
								})
							flag=false;
						    break;
					}
					for(let j=0;j<data[i].people.length;j++){
						if(j!=data[i].people.length-1){
							string+=data[i].people[j]+"、"
						}else{
							string+=data[i].people[j]
						}
					}
					data[i].strings=string
				}
				if(flag){
					this.data=data
					this.show=true
				}
			},
			back(e){
				console.log(e)
				this.$data.show=false
			},
		},
		components:{
			popup
		}
	}
</script>

<style>
	.box {
		width: 80%;
		min-height: 80vh;
		margin-left: 10%;
		border: 1px solid #999999;
		box-sizing: border-box;
	}

	.marginTop {
		margin-top: 40rpx;
	}
    .margin{
		margin:40rpx 0;
	}
	.title {
		text-align: center;
		font-weight: 900;
		font-size: 60rpx;
	}

	.el-checkbox {
		font-size: 40rpx;
	}

	.el-checkbox__input {
		font-size: 40rpx;
	}

	/deep/ .el-checkbox__label {
		font-size: 40rpx !important;
	}

	/* 	/deep/ .el-checkbox__inner{
		width: 40rpx;
		height: 40rpx;
	} */
	.operationItem {
		margin: 40rpx 10rpx;
		padding: 10rpx 20rpx;
		/* background-color: #000000; */
		/* border: 1px solid #409EFF; */
		box-shadow: 1px 2px 8px #C8CBC8;
		
	}

    .IiemBox{
		display: flex;
	}
	.itemUi {
		flex: 3;
		/* border: 1px solid black; */
		padding: 20rpx 20rpx;
	}

	.itemSum {
		flex: 1;
		/* border: 1px solid pink; */
		display: flex;
		align-items: center;
		justify-content: center;
	}

	/deep/ .el-popover {
		position: relative;
	}
	.itemData{
		font-size: 35rpx;
	}
	.button {
		display: flex;
		justify-content: space-around;
	}
	
	.btn {
		padding: 20rpx 40rpx;
		color: #000;
		/* background-color: #f8f8f8; */
		font-size: 36rpx;
		cursor: pointer;
	}
</style>
